.description {
    $fs: $base-px-desktop;
    $fs-mb: $base-px-mb;

    font-size: vw($fs);
    width: 100%;
    height: auto;
    background-color: $color-bg-light;
    padding: em(70, $fs) 0;

    @media (--viewport-mobile-pt) {
        font-size: vw($fs-mb, true);
        padding: em(84, $fs-mb) 0;
    }

    &__ul {
        display: flex;
        flex-direction: column;
        gap: em(33, $fs);
    }

    &__li {
        display: flex;
        flex-direction: column;
        gap: em(25, $fs);
    }

    &__title {
        font-size: em(30, $fs);
        line-height: em(37, 30);
        letter-spacing: 0.6px;
        color: $color-text-accent;

        @media (--viewport-mobile-pt) {
            font-size: em(22, $fs-mb);
            font-weight: $font-weight-default;
            line-height: 125%;
            letter-spacing: 0.44px;
        }
    }

    &__text {
        line-height: em(24, $fs);
        letter-spacing: 0.36px;
    }
}
