.decor-noise {
    $fs: 40;
    $fs-mb: 35;

    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;

    &,
    &__anim,
    &__transform,
    &__el {
        width: 100%;
        height: 100%;
    }

    .entered {
        &.decor-noise__anim {
            animation: swing-y 0.05s linear 40;

            @keyframes swing-y {
                0% {
                    transform: translateY(-0.1px);
                }

                2% {
                    transform: translateY(0.1px);
                }

                4% {
                    transform: translateY(-0.1px);
                }

                6% {
                    transform: translateY(-0.1px);
                }

                8% {
                    transform: translateY(0.1px);
                }

                10% {
                    transform: translateY(-0.1px);
                }

                50% {
                    transform: translateY(-0.1px);
                }

                96% {
                    transform: translateY(-0.1px);
                    transition-timing-function: ease-in-out;
                }

                98% {
                    transform: translateY(0.1px);
                    transition-timing-function: ease-in-out;
                }

                100% {
                    transform: translateY(-0.1px);
                    transition-timing-function: ease-in-out;
                }
            }
        }

        &.decor-noise__transform {
            animation: swing-x 0.05s linear 40;

            @keyframes swing-x {
                0% {
                    transform: translateX(-0.1px);
                }

                2% {
                    transform: translateX(0.1px);
                }

                4% {
                    transform: translateX(-0.1px);
                }

                6% {
                    transform: translateX(-0.1px);
                }

                8% {
                    transform: translateX(0.1px);
                }

                10% {
                    transform: translateX(-0.1px);
                }

                50% {
                    transform: translateX(-0.1px);
                }

                96% {
                    transform: translateX(-0.1px);
                    transition-timing-function: ease-in-out;
                }

                98% {
                    transform: translateX(0.1px);
                    transition-timing-function: ease-in-out;
                }

                100% {
                    transform: translateX(-0.1px);
                    transition-timing-function: ease-in-out;
                }
            }
        }
    }

    &__el {
        width: 110%;
        height: 110%;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url("@/assets/images/noise.png");
    }
}
