@use "sass:math";

// colors
$color-bg: #000341;
$color-bg-light: #eee7fe;
$color-text: #000;
$color-text-light: #fff;
$color-text-light-2: #b1b1b1;
$color-text-accent-light: #636eb4;
$color-text-accent: #0022a9;
$color-text-accent-2: #88daff;
$color-text-accent-3: #55daff;

// text
$font-default: "Montserrat", system-ui, sans-serif;
$font-accent-feature-mono: "FeatureMono", system-ui, sans-serif;
$font-accent-tablon: "Tablon", system-ui, sans-serif;

// Basing Weights
$font-weight-default: 400;
$font-weight-accent: 500;
$font-weight-bold: 700;
$base-px-desktop: 18;
$base-width-desktop: 1440;
$base-height-desktop: 680;
$max-width-desktop: 1440;

// add
$base-px-mb: 16;
$base-width-mb: 375;
$base-height-mb: 812;

// Font sizes
// to calc 1rem with poly-fluid-sizing
$map-font-size-lp: (
    560px: 18px,
    850px: 18px,
    852px: 18px,
    1440px: 18px
);
$map-font-size-pt: (
    320px: 14px,
    375px: 16px,
    425px: 17px,
    1024px: 18px
);

// Add one more variable if mobile line-height differs from desktop one
$line-height-default: math.div(24, $base-px-desktop);

// Line height of accent font family text
$line-height-accent: math.div(126, 140);
$padding-x-content-desktop: em(72, $base-px-desktop);
$padding-x-content-mobile: em(30, $base-px-mb);
