.block {
    $fs: $base-px-desktop;
    $fs-mb: $base-px-mb;

    font-size: vw($fs);
    width: 100%;
    height: auto;

    @media (--viewport-mobile-pt) {
        font-size: vw($fs-mb, true);
        font-weight: $font-weight-default;
        line-height: 125%;
        letter-spacing: 0.44px;
    }

    &-1 {
        width: 100%;
        height: auto;
        padding-top: em(67, $fs);
        padding-bottom: em(67, $fs);
        background-image: url("@/assets/images/main/gradient-bg-block2.png");
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;

        @media (--viewport-mobile-pt) {
            padding-top: em(92, $fs-mb);
            padding-bottom: em(76, $fs-mb);
            background-image: url("@/assets/images/main/gradient-bg-block2-mb.png");
        }

        .container {
            position: relative;
            z-index: 2;
        }

        .info-module {
            display: flex;
            flex-direction: column;
            gap: em(25, $fs);
            border-radius: em(20, $fs);
            border: 1px solid $color-text-accent-3;
            padding: em(50, $fs) em(70, $fs);
            background: rgb(0 28 99 / 26%);
            color: $color-text-accent-3;
            position: relative;

            @media (--viewport-mobile-pt) {
                padding: em(51, $fs-mb) em(16, $fs-mb) em(217, $fs-mb) em(23, $fs-mb);
            }

            &__decor {
                position: absolute;
                top: 50%;
                right: em(98, $fs);
                transform: translateY(-50%);
                width: em(472, $fs);
                height: em(328, $fs);
                background-image: url("@/assets/images/main/tablet.png");
                background-repeat: no-repeat;
                background-size: cover;

                @media (--viewport-mobile-pt) {
                    top: auto;
                    bottom: em(-15, $fs-mb);
                    right: 0;
                    transform: none;
                    width: em(303, $fs-mb);
                    height: em(210, $fs-mb);
                }
            }

            &__title {
                font-size: em(65, $fs);
                line-height: 100%;
                letter-spacing: 1.625px;
                text-transform: uppercase;

                @media (--viewport-mobile-pt) {
                    display: flex;
                    flex-direction: column;
                    font-size: em(65, $fs-mb);
                    font-weight: $font-weight-accent;
                    line-height: 100%;
                    letter-spacing: 1.625px;
                    text-transform: uppercase;

                    span {
                        font-size: em(30, 65);
                    }
                }
            }

            &__text {
                font-size: em(30, $fs);
                width: em(620, 30);
                line-height: 150%;

                @media (--viewport-mobile-pt) {
                    width: auto;
                    font-size: vw(22, true);
                    font-weight: $font-weight-default;
                    line-height: 125%;
                    letter-spacing: 0.44px;
                }
            }
        }

        &__title {
            font-size: em(30, $fs);
            line-height: 125%;
            letter-spacing: 0.6px;
            color: $color-text-light;
            margin-top: em(55, 30);
            margin-bottom: em(26, 30);

            @media (--viewport-mobile-pt) {
                font-size: vw(22, true);
                font-weight: $font-weight-default;
                line-height: 125%;
                letter-spacing: 0.44px;
            }
        }

        &__text {
            line-height: 145%;
            letter-spacing: 0.36px;
            color: $color-text-light;
        }
    }

    &-2 {
        width: 100%;
        height: auto;
        padding-top: em(52, $fs);
        padding-bottom: em(72, $fs);
        background-color: $color-bg-light;

        @media (--viewport-mobile-pt) {
            padding-top: em(89, $fs-mb);
            padding-bottom: em(96, $fs-mb);
        }

        &__ul {
            margin-top: em(27, $fs);

            @media (--viewport-mobile-pt) {
                margin-top: em(39, $fs-mb);
            }
        }

        &__li {
            display: flex;
            align-items: center;
            gap: em(30, $fs);

            @media (--viewport-mobile-pt) {
                align-items: flex-start;
                gap: em(20, $fs-mb);
            }

            &:not(:last-child) {
                margin-bottom: em(26, $fs);

                @media (--viewport-mobile-pt) {
                    margin-bottom: em(33, $fs-mb);
                }
            }
        }

        &__title {
            font-size: em(30, $fs);
            line-height: 125%;
            letter-spacing: 0.6px;
            color: $color-text-accent;

            @media (--viewport-mobile-pt) {
                font-size: vw(22, true);
                font-weight: $font-weight-default;
                line-height: 125%;
                letter-spacing: 0.44px;
            }
        }

        &__p {
            flex: 1;
        }

        .cross-mixin {
            width: em(39, $fs);
            height: em(39, $fs);
            opacity: 0;
            clip-path: polygon(46% 49%, 45% 50%, 45% 50%, 46% 49%, 45% 49%, 47% 51%, 45% 49%, 45% 49%, 47% 50%, 46% 51%, 46% 50%, 47% 50%);
            transition: clip-path 1s ease-in-out, opacity 0.2s ease-out;

            &.entered {
                opacity: 1;
                clip-path: polygon(0 0, 0% 20%, 0 49%, 0% 80%, 0 100%, 50% 100%, 100% 100%, 100% 80%, 100% 51%, 100% 20%, 100% 0, 51% 0);
                transition: clip-path 1s ease-in-out, opacity 0.2s ease-out;
            }

            @media (--viewport-mobile-pt) {
                width: vw(47, true);
                height: vw(47, true);
            }
        }

        .comment {
            position: relative;
            margin-top: em(96, $fs);

            &__borders,
            &__border {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            &__border {
                border-radius: em(24, $fs);

                &-transform,
                &-el {
                    border-radius: inherit;
                    width: 100%;
                    height: 100%;
                    transition: transform 0.6s ease-out, opacity 0.8s ease-out;
                }

                &-el {
                    border-radius: inherit;
                    border: em(2, $fs) solid $color-text-accent;
                    opacity: 0;
                }

                &:first-child {
                    top: em(-10, $fs);
                    left: em(10, $fs);

                    @media (--viewport-mobile-pt) {
                        top: vw(-15, true);
                        left: vw(15, true);
                    }

                    .comment__border-transform {
                        transform: translateX(2%);
                    }

                    .comment__border-el {
                        transform: translateY(-2%);
                    }

                    .comment__border-el,
                    .comment__border-transform, {
                        &.entered {
                            opacity: 1;
                            transition: transform 0.6s ease-out 0.3s, opacity 0.8s ease-out 0.3s;
                            transform: translate(0, 0) scale(1);
                        }
                    }
                }

                &:last-child {
                    top: em(-20, $fs);
                    left: em(20, $fs);

                    @media (--viewport-mobile-pt) {
                        display: none;
                    }

                    .comment__border-transform {
                        transform: translateX(2%);
                    }

                    .comment__border-el {
                        transform: translateY(-2%);
                    }

                    .comment__border-el,
                    .comment__border-transform, {
                        &.entered {
                            opacity: 1;
                            transition: transform 0.6s ease-out, opacity 0.8s ease-out;
                            transform: translate(0, 0) scale(1);
                        }
                    }
                }
            }

            &__wrap {
                background-color: #fff;
                border-radius: em(24, $fs);
                border: em(2, $fs) solid $color-text-accent;
                position: relative;
                z-index: 2;
                opacity: 0;
                transform: translate(1%, -2%);

                &.entered {
                    opacity: 1;
                    transition: transform 0.6s ease-out 0.9s, opacity 0.5s ease-out 0.9s;
                    transform: translate(0, 0) scale(1);
                }
            }

            &__header {
                padding: em(19, $fs) em(30, $fs) em(19, $fs) em(65, $fs);
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: em(2, $fs) solid $color-text-accent;

                @media (--viewport-mobile-pt) {
                    padding: vw(29, true) 0 vw(24, true) vw(19, true);
                }
            }

            &__buttons {
                display: flex;
                align-items: center;
                gap: em(15.5, $fs);

                @media (--viewport-mobile-pt) {
                    display: none;
                }
            }

            &__button {
                width: em(22.8, $fs);
                height: em(22.8, $fs);
                background: linear-gradient(90deg, #55daff 0%, #636eb4 100%);
                border-radius: 100%;
            }

            &__first-title {
                font-size: em(30, $fs);
                line-height: 125%;
                letter-spacing: 0.6px;
                color: $color-text-accent-light;
                transition-delay: 1s;

                &.entered {
                    transition-delay: 1s;
                }

                @media (--viewport-mobile-pt) {
                    font-size: vw(22, true);
                    font-weight: $font-weight-default;
                    line-height: 125%;
                    letter-spacing: 0.44px;
                }
            }

            &__body,
            &__footer {
                position: relative;
                padding-left: em(167, $fs);
                padding-right: em(235, $fs);

                @media (--viewport-mobile-pt) {
                    padding-left: vw(19, true);
                    padding-right: vw(19, true);
                }
            }

            &__quotation-marks {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;

                @media (--viewport-mobile-pt) {
                    display: none;
                }
            }

            .quotation-marks {
                position: absolute;
                width: em(47, $fs);
                height: em(41, $fs);
                opacity: 0;
                transition-delay: 1.04s;

                &.entered {
                    opacity: 1;
                    transition: opacity 0.8s ease-out 1.04s;
                }

                &:first-child {
                    left: em(73, $fs);
                    top: calc(50% - em(20, $fs));
                    transform: translateY(-50%);
                }

                &:last-child {
                    top: 0;
                    right: em(195, $fs);
                }
            }

            &__title {
                margin-top: em(80, 35);
                margin-bottom: em(31, 35);
                font-size: em(35, $fs);
                font-weight: $font-weight-accent;
                color: $color-text-accent;
                line-height: 120%;
                letter-spacing: 0.875px;
                text-transform: uppercase;
                transition-delay: 1.02s;

                &.entered {
                    transition-delay: 1.02s;
                }

                @media (--viewport-mobile-pt) {
                    font-size: vw(26, true);
                    letter-spacing: 0.65px;
                    margin-top: em(30, 26);
                    margin-bottom: em(10, 26);
                }
            }

            &__quote {
                margin-bottom: em(31, $fs);
                transition-delay: 1.04s;

                &.entered {
                    transition-delay: 1.04s;
                }

                @media (--viewport-mobile-pt) {
                    margin-bottom: em(26, $fs-mb);
                }
            }

            &__author {
                font-size: em(30, $fs);
                color: $color-text-accent-light;
                transition-delay: 1.06s;

                &.entered {
                    transition-delay: 1.06s;
                }

                @media (--viewport-mobile-pt) {
                    font-size: vw(22, true);
                    font-weight: $font-weight-default;
                    line-height: 125%;
                    letter-spacing: 0.44px;
                }
            }

            &__post {
                color: $color-text-light-2;
                line-height: 145%;
                letter-spacing: 0.36px;
                transition-delay: 1.08s;

                &.entered {
                    transition-delay: 1.08s;
                }

                @media (--viewport-mobile-pt) {
                    letter-spacing: 0.32px;
                }
            }

            &__footer {
                padding-bottom: em(64, $fs);
                display: flex;
                align-items: center;
                justify-content: space-between;

                @media (--viewport-mobile-pt) {
                    padding-bottom: em(42, $fs-mb);
                    flex-direction: column;
                    align-items: flex-start;
                    gap: em(23, $fs-mb);
                }
            }

            .signature {
                width: em(216, $fs);
                height: em(59, $fs);
                opacity: 0;
                clip-path: polygon(0 71%, 0 55%, 9% 60%, 9% 69%, 14% 76%, 9% 83%, 0 81%);
                transition: clip-path 1.3s ease-out, opacity 0.8s ease-out;
                transition-delay: 1.2s;

                &.entered {
                    clip-path: polygon(0 0, 59% 0, 100% 0, 100% 54%, 100% 100%, 44% 100%, 0 100%);
                    opacity: 1;
                    transition: clip-path 1.3s ease-out, opacity 0.8s ease-out;
                    transition-delay: 1.2s;
                }

                @media (--viewport-mobile-pt) {
                    width: em(180, $fs-mb);
                    height: em(49, $fs-mb);
                    margin-left: auto;
                }

                &__el {
                    width: 100%;
                    height: 100%;
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-image: url("@/assets/images/main/signature.png");
                }
            }
        }
    }

    &-3 {
        width: 100%;
        height: auto;
        padding-top: em(67, $fs);
        padding-bottom: em(60, $fs);
        background-image: url("@/assets/images/main/gradient-bg-block4.png");
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;

        @media (--viewport-mobile-pt) {
            padding-top: em(80, $fs-mb);
            padding-bottom: em(55, $fs-mb);
            background-image: url("@/assets/images/main/gradient-bg-block4-mb.png");
        }

        .container {
            position: relative;
            z-index: 2;
        }

        &__title {
            color: $color-text-accent-3;
            font-size: em(30, $fs);
            line-height: 125%;
            letter-spacing: 0.6px;
            padding-bottom: em(33, 30);

            @media (--viewport-mobile-pt) {
                font-size: vw(22, true);
                font-weight: $font-weight-default;
                line-height: 125%;
                letter-spacing: 0.44px;
                padding-bottom: em(25, 22);
            }
        }

        &__text {
            line-height: 145%;
            letter-spacing: 0.36px;
            color: $color-text-light;
            padding-bottom: em(33, $fs);
        }
    }

    &-4 {
        width: 100%;
        height: auto;
        padding-top: em(80, $fs);
        padding-bottom: em(106, $fs);
        background-color: $color-bg-light;

        @media (--viewport-mobile-pt) {
            padding-top: em(80, $fs-mb);
            padding-bottom: em(97, $fs-mb);
        }

        &__first-title {
            font-size: em(30, $fs);
            color: $color-text-light-2;
            margin-bottom: em(44, 30);
            text-align: center;

            @media (--viewport-mobile-pt) {
                display: none;
            }
        }

        &__title {
            font-size: em(45, $fs);
            font-weight: $font-weight-accent;
            color: $color-text-accent;
            text-align: center;
            text-transform: uppercase;

            @media (--viewport-mobile-pt) {
                font-size: em(35, $fs-mb);
                line-height: 115%;
                letter-spacing: 0.87px;
                text-align: left;
            }
        }

        &__ul {
            margin-top: em(56, $fs);
        }

        &__li {
            display: flex;
            align-items: flex-start;
            gap: em(20, $fs);

            @media (--viewport-mobile-pt) {
                flex-direction: column;
            }

            &:not(:last-child) {
                margin-bottom: em(26, $fs);

                @media (--viewport-mobile-pt) {
                    margin-bottom: em(50, $fs-mb);
                }
            }
        }

        &__p {
            flex: 1;
        }

        .check-mixin {
            width: em(39, $fs);
            height: em(39, $fs);
            opacity: 0;
            clip-path: polygon(8% 8%, 92% 8%, 92% 92%, 8% 92%, 1% 89%, 0% 100%, 100% 100%, 100% 0%, 0% 0%, 0% 100%, 7% 99%);
            transition: clip-path 1s ease-out, opacity 0.2s ease-out;

            &.entered {
                opacity: 1;
                clip-path: polygon(8% 8%, 92% 8%, 92% 92%, 93% 6%, 7% 8%, 0% 100%, 100% 100%, 100% 0%, 0% 0%, 0% 100%, 7% 99%);
                transition: clip-path 1s ease-out, opacity 0.2s ease-out;
            }

            @media (--viewport-mobile-pt) {
                width: em(45, $fs-mb);
                height: em(45, $fs-mb);
            }
        }
    }
}
