.footer {
    $fs: $base-px-desktop;
    $fs-mb: $base-px-mb;

    font-size: vw($fs);
    width: 100%;
    padding-top: em(30, $fs);
    padding-bottom: em(30, $fs);
    background-color: $color-text-accent-light;

    @media (--viewport-mobile-pt) {
        font-size: vw($fs-mb, true);
        padding-top: em(30, $fs-mb);
        padding-bottom: em(30, $fs-mb);
    }

    &__advertiser {
        color: $color-text-light;
        text-align: center;
        line-height: 145%;
        letter-spacing: 0.36px;

        @media (--viewport-mobile-pt) {
            font-size: em(16, $fs-mb);
            line-height: 145%;
            letter-spacing: 0.32px;
            text-align: left;
        }
    }
}
