.header {
    $fs: 40;
    $fs-mb: 35;
    $transition-timing-function: cubic-bezier(0.17, 0.67, 0.83, 1.02);

    font-size: vw($fs);
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;

    @media (--viewport-mobile-pt) {
        font-size: vw($fs-mb, true);
    }

    &__decor,
    .decor {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .decor {
        &,
        &__anim,
        &__transform,
        &__el {
            width: 100%;
            height: 100%;
        }

        &,
        &__el {
            background-repeat: no-repeat;
        }

        &--gradient {
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url("@/assets/images/header/gradient-bg.png");

            @media (--viewport-mobile-pt) {
                background-image: url("@/assets/images/header/gradient-bg-mb.png");
            }
        }

        &--grid {
            background-size: cover;
            background-repeat: no-repeat;
            background-image: url("@/assets/images/header/grid-bg.png");
        }

        &--disk {
            &-1 {
                width: vw-vh-cover(684);
                height: vw-vh-cover(855);
                top: em(-66, $fs);
                left: auto;
                right: 0;

                @media (--viewport-mobile-pt) {
                    width: em(557.49, $fs-mb);
                    height: em(697, $fs-mb);
                    top: 50%;
                    right: 50%;
                    transform: translate(50%, -50%);
                }

                .decor__transform {
                    opacity: 0;
                    transform: scale(0.8);
                    transition: transform 7s $transition-timing-function 0.5s, opacity 9s ease-out;

                    &.entered {
                        opacity: 1;
                        transition: transform 7s $transition-timing-function 0.5s, opacity 9s ease-out;
                        transform: translate(0, 0) scale(1);
                    }
                }

                .decor__el {
                    background-image: url("@/assets/images/header/disk.png");
                    background-size: contain;
                    background-position: center;
                }
            }

            &-2 {
                width: vw-vh-cover(305);
                height: vw-vh-cover(266);
                top: em(80, $fs);
                left: em(60, $fs);

                .decor__transform {
                    opacity: 0;
                    transform: scale(0.8);
                    transition: transform 8s $transition-timing-function, opacity 8s ease-out;

                    &.entered {
                        opacity: 1;
                        transition: transform 8s $transition-timing-function, opacity 8s ease-out;
                        transform: translate(0, 0) scale(1);
                    }
                }

                .decor__el {
                    background-image: url("@/assets/images/header/disk-mini.png");
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                    opacity: 0.7;
                    transform: rotate(5deg);
                }

                @media (--viewport-mobile-pt) {
                    display: none;
                }
            }
        }

        &--castle {
            width: em(562, $fs);
            height: em(385.1, $fs);
            top: 45%;
            left: auto;
            right: em(87, $fs);
            transform: translateY(-50%);

            @media (--viewport-mobile-pt) {
                width: vw-vh-contain(314.9, true);
                height: vw-vh-contain(356.31, true);
                top: 50%;
                right: 50%;
                transform: translate(50%, -50%);
            }

            .decor__wrap {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%, -50%);
            }

            .decor__bg {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%, -50%) scale(0.95);
                background-image: url("@/assets/images/header/castle-bg.png");
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                opacity: 0;

                &.entered {
                    opacity: 1;
                    transition: transform 2s ease-in-out 0.2s, opacity 0.5s ease-out 0.4s;
                    transform: translate(50%, -50%) scale(1);
                }

                @media (--viewport-mobile-pt) {
                    background-image: url("@/assets/images/header/castle-bg-mb.png");
                }
            }

            .decor__anim {
                width: em(286.92, $fs);
                height: em(370.64, $fs);
                position: absolute;
                top: 50%;
                right: 40%;
                transform: translate(50%, -50%);

                @media (--viewport-mobile-pt) {
                    top: 57%;
                }
            }

            .decor__transform {
                animation: swing-y 6s ease-in-out infinite;

                @keyframes swing-y {
                    0% {
                        transform: translateY(-0.3%);
                    }

                    50% {
                        transform: translateY(0.3%);
                    }

                    100% {
                        transform: translateY(-0.3%);
                    }
                }
            }

            .decor__el {
                background-image: url("@/assets/images/header/castle.png");
                background-size: contain;
                background-position: center;
                background-repeat: no-repeat;
                animation: swing-x 3s ease-in-out infinite;

                @keyframes swing-x {
                    0% {
                        transform: translateX(-0.3%);
                    }

                    50% {
                        transform: translateX(0.3%);
                    }

                    100% {
                        transform: translateX(-0.3%);
                    }
                }

                @media (--viewport-mobile-pt) {
                    width: vw-vh-contain(222.38, true);
                    height: vw-vh-contain(287.26, true);
                }

                &.entered {
                    opacity: 1;
                    transition-delay: 0.5s;
                    transition-duration: 2s;
                    transition-timing-function: $transition-timing-function;
                    transform: translate(0, 0) scale(1);
                }
            }

            .star-mixin {
                position: absolute;
                bottom: 0;
                left: 0;
                opacity: 0;

                &.entered {
                    opacity: 1;
                    transition: opacity 0.3s ease-out;
                }

                &__transform {
                    animation: swing-y 9s ease-in-out infinite 0.3s;

                    @keyframes swing-y {
                        0% {
                            transform: translateY(-0.3%);
                        }

                        50% {
                            transform: translateY(0.3%);
                        }

                        100% {
                            transform: translateY(-0.3%);
                        }
                    }
                }

                &__element {
                    animation: scale 9s ease-in-out infinite;

                    @keyframes scale {
                        0% {
                            transform: scale(1);
                        }

                        50% {
                            transform: scale(0.8);
                        }

                        100% {
                            transform: scale(1);
                        }
                    }
                }

                &__anim {
                    animation: swing-x 2s ease-in-out infinite;

                    @keyframes swing-x {
                        0% {
                            transform: translateX(-0.3%);
                        }

                        50% {
                            transform: translateX(0.3%);
                        }

                        100% {
                            transform: translateX(-0.3%);
                        }
                    }
                }

                &.star--1 {
                    width: em(83.06, $fs);
                    height: em(83.06, $fs);
                    bottom: em(106, $fs);

                    @media (--viewport-mobile-pt) {
                        width: vw-vh-contain(53.37, true);
                        height: vw-vh-contain(53.37, true);
                        bottom: 93%;
                        left: auto;
                        right: 0;
                    }

                    .star-mixin__element {
                        transition-delay: 0.7s;
                    }
                }

                &.star--2 {
                    width: em(39.01, $fs);
                    height: em(39.01, $fs);
                    bottom: em(64, $fs);
                    left: em(62, $fs);

                    @media (--viewport-mobile-pt) {
                        width: vw-vh-contain(30.24, true);
                        height: vw-vh-contain(30.24, true);
                        bottom: 79%;
                        left: auto;
                        right: 21%;
                    }

                    .star-mixin__transform {
                        transition-delay: 0.7s;
                    }
                }
            }
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        font-size: 1em;
        position: relative;
        z-index: 2;
    }

    &__title {
        width: em(800, $fs);
        display: flex;
        flex-direction: column;
        padding-left: em(68, $fs);
        gap: em(15, $fs);

        @media (--viewport-mobile-pt) {
            height: 100%;
            padding: em(63, $fs-mb) em(20, $fs-mb);
            justify-content: space-between;
            align-items: center;
            text-align: center;
        }
    }

    &__h1 {
        font-size: em(80, $fs);
        font-weight: $font-weight-accent;
        color: $color-text-light;
        line-height: em(88, 80);
        letter-spacing: em(1.6, 80);
        text-transform: uppercase;
        opacity: 0;
        clip-path: polygon(0 0, 0 70%, 0 100%, 0% 100%, 0 48%, 0% 0%);
        transition: clip-path 1.3s ease-out, opacity 0.2s ease-out;

        &.entered {
            opacity: 1;
            clip-path: polygon(0 0, 0 70%, 0 100%, 100% 100%, 100% 49%, 100% 0);
            transition: clip-path 1.3s ease-out, opacity 0.2s ease-out;
        }

        @media (--viewport-mobile-pt) {
            font-size: em(45, $fs-mb);
        }
    }

    &__subtitle {
        font-size: em(40, $fs);
        color: $color-text-accent-2;
        line-height: em(52, 40);
        letter-spacing: em(0.8, 40);
        opacity: 0;
        clip-path: polygon(55% 100%, 0 0, 100% 86%, 3% 72%, 91% 33%, 61% 0, 0 34%, 100% 100%, 0 49%, 100% 0, 100% 63%, 46% 0);
        transition: clip-path 3s ease-out, opacity 0.4s ease-out 0.9s;

        &.entered {
            opacity: 1;
            clip-path: polygon(20% 0%, 0 0, 0 48%, 0 100%, 20% 100%, 51% 100%, 80% 100%, 100% 100%, 100% 51%, 100% 0, 80% 0%, 46% 0);
            transition: clip-path 3s ease-out, opacity 0.4s ease-out 0.9s;
        }
    }
}
